.test{
    width:100%;
    border-radius: 14px 0 14px 0;
    margin: 30px;
    border: none;
    height:100px;
    /* box-sizing: content-box; */
    background-color: antiquewhite;
    box-shadow: 5px 5px 5px 5px rgb(150 150 150 / 50%);
    font-size: 60px;
}
.profile{

position: relative;

height: 157px;

width: 100%;

/* background: red; */
}
.profile .profileImage{
position: absolute;
top: 10%;
right: 5%;
background: purple;
height: 110px;
width: 110px;
border-radius: 29%;
border: 1px solid aqua;
}
.profile #username{

position: absolute;

top: 17%;

direction: rtl;

right: 150px;

font-size: 21px;

padding: 3px;

border-radius: 8px;
}
.profile #info{
position: absolute;
top: 46%;
direction: rtl;
right: 147px;
font-size: 17px;
padding: 3px;
border-radius: 8px;
max-width: 170px;
line-height: 25px;
}

.horizontalItems{

position: relative;

height: 45px;

/* background: purple; */

border-bottom: 1px solid dimgray;

overflow: hidden;

display: flex;

overflow-x: auto;

padding: 0px 11px 0px 11px;

width: 100%;
}
.horizontalItems .item{
    
position: relative;
    
height: 100%;
    
background: transparent;
    
display: inline-flex;
    
justify-content: center;
    
align-items: center;
    
padding: 0px 6px 0px 6px;
    
color: white;
    
margin: 0px;
}
.horizontalItems .item.active{
    border-bottom: 3px solid green;
}
#dataContainer .empty{
    
display: inline;
    
position: absolute;
    
top: 50%;
    
left: 50%;
    
transform: translate(-50%,-50%);
    
border-radius: 19px;
    
padding: 10px;
    
text-align: left;
}
#filter{
    left: 10%;
    position: relative;
    width: 55%;
    margin-top: 12px;
    background: #d3d3d338;
    display: inline-block;
    margin-bottom: 13px;
}
.select_box_container .title{
    
display: inline-block;
    
position: relative;
    
margin-left: 24%;
}